module.exports = "## Drawer\n\nThe Navigation Drawer is used to organize access to destinations and other functionality on an app.\n\n## Usage\n\n```html\n <m-drawer\n  show\n  position=\"left\">\n  <m-list\n    single-selection\n    css=\"ul{\n      width:200px;\n      height:100vh;\n      border: 1px solid rgba(0,0,0,.1);\n      background:#fff;\n    }\"\n    items=\"...\">\n  </m-list>\n</m-drawer>\n```\n\n## Usage in Omi\n\nJSX:\n\n```jsx\n<m-drawer\n  show={this.show}\n  position='left'\n  onClose={this.onClose}>\n  <m-list\n    singleSelection\n    css={`ul{\n      width:200px;\n      height:100vh;\n      border: 1px solid rgba(0,0,0,.1);\n      background:#fff;\n    }`}\n    items={this.items}\n    onItemClick={this.onItemClick}>\n  </m-list>\n</m-drawer>\n```\n\n## API\n\n### Props\n\n```jsx\n{\n  position?: 'left' | 'right' | 'top' | 'bottom',\n  show?: boolean\n}\n```"

